.modal.disabled {
  animation: noop !important;
}
.modal.disabled.am-fade-and-slide-top .modal-dialog {
  animation-duration: 0.3s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: backwards;
  animation-name: fadeAndSlideFromTop;
}
.modal.disabled.am-fade-and-slide-top.ng-enter .modal-dialog {
  visibility: hidden;
  animation-name: fadeAndSlideFromTop;
}
.modal.disabled.am-fade-and-slide-top.ng-enter-active .modal-dialog,
.modal.disabled.am-fade-and-slide-top.ng-leave-active .modal-dialog {
  visibility: visible;
}
.modal.disabled.am-fade-and-slide-top.ng-leave .modal-dialog {
  animation-name: fadeAndSlideToTop;
}
.modal.disabled.am-fade-and-scale .modal-dialog {
  animation-duration: 0.3s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: backwards;
  animation-name: fadeAndScaleIn;
}
.modal.disabled.am-fade-and-scale.ng-enter .modal-dialog {
  visibility: hidden;
  animation-name: fadeAndScaleIn;
}
.modal.disabled.am-fade-and-scale.ng-enter-active .modal-dialog,
.modal.disabled.am-fade-and-scale.ng-leave-active .modal-dialog {
  visibility: visible;
}
.modal.disabled.am-fade-and-scale.ng-leave .modal-dialog {
  animation-name: fadeAndScaleOut;
}
@keyframes noop {
  from {
    opacity: 1;
  }
  to {
    opacity: 1;
  }
}
input.ng-dirty.ng-invalid,
textarea.ng-dirty.ng-invalid,
select.ng-dirty.ng-invalid {
  color: #b94a48;
  border-color: #ee5f5b;
}
input.ng-dirty.ng-invalid:focus,
textarea.ng-dirty.ng-invalid:focus,
select.ng-dirty.ng-invalid:focus {
  border-color: #e9322d;
  -webkit-box-shadow: 0 0 6px #f8b9b7;
  box-shadow: 0 0 6px #f8b9b7;
}
input:focus.ng-pristine.ng-invalid,
textarea:focus.ng-pristine.ng-invalid,
select:focus.ng-pristine.ng-invalid {
  color: #555555;
  border: 1px solid #cccccc;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  -webkit-transition: border linear .2s, box-shadow linear .2s;
  -o-transition: border linear .2s, box-shadow linear .2s;
  transition: border linear .2s, box-shadow linear .2s;
}
input:focus.ng-pristine.ng-invalid:focus,
textarea:focus.ng-pristine.ng-invalid:focus,
select:focus.ng-pristine.ng-invalid:focus {
  border-color: rgba(82, 168, 236, 0.8);
  -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(82,168,236,.6);
  box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(82,168,236,.6);
}
