body {
  position: relative;
  /* For scrollyspy */
  padding-top: 50px;
  /* Account for fixed navbar */
}
/* Tweak display of the code snippets when following an example */
.bs-example-scope {
  font-size: 85%;
}
.bs-example-scope + .bs-example {
  margin: -15px -15px 15px;
}
.bs-example-scope + .bs-example + .highlight > pre {
  border-radius: 0 0 4px 4px;
}
.aside.bs-docs-aside .aside-header {
  background: #b94846;
}
.aside.bs-docs-aside .nav {
  margin: 0px;
}
.bs-docs-social {
  max-height: 200px;
}
.bs-docs-social ul {
  list-style: none;
  padding: 1em 0 0 0;
}
.bs-docs-social ul li {
  height: 30px;
}
@media (max-width: 991px) {
  .bs-docs-social ul li {
    display: inline-block;
  }
}
/*
 * Topography, yo!
 *
 * Apply the map background via base64 and relevant colors where we need 'em.
 */
.bs-docs-home,
.bs-header {
  color: white;
  background: #b94846 url("../images/triangular-red.png");
  padding-bottom: 20px;
}
/* Echo out a label for the example */
pre > code {
  background: transparent;
}
.bs-example + .highlight {
  position: relative;
}
.bs-example + .highlight code {
  padding: 2.5em 0.5em 1em;
}
.bs-example + .highlight:after {
  content: "Source";
  position: absolute;
  top: 15px;
  left: 15px;
  font-size: 12px;
  font-weight: bold;
  color: #bbb;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.edit-plunkr,
a[ng-click] {
