}
input[type="checkbox"] {
  cursor: pointer;
}
.twitter-follow-button {
  position: relative;
  top: 5px;
  margin-left: 10px;
}
.tab-pane.am-fade {
  animation-duration: .3s;
  animation-timing-function: ease;
  animation-fill-mode: backwards;
  opacity: 1;
}
.tab-pane.am-fade.active-remove {
  display: none !important;
}
.tab-pane.am-fade.active-add {
  animation-name: fadeIn;
}
.panel-collapse.am-collapse {
  animation-duration: .3s;
  animation-timing-function: ease;
  animation-fill-mode: backwards;
  overflow: hidden;
}
.panel-collapse.am-collapse.in-remove {
  animation-name: collapse;
  display: block;
}
.panel-collapse.am-collapse.in-add {
  animation-name: expand;
}
.panel-collapse.am-collapse2 {
  transition: 0.3s ease max-height;
  max-height: 100px;
  overflow: hidden;
}
.panel-collapse.am-collapse2.in-remove {
  max-height: 100px;
  display: block;
}
.panel-collapse.am-collapse2.in-add {
  max-height: 0px;
}
@keyframes expand {
  from {
    max-height: 0px;
  }
  to {
    max-height: 100px;
  }
}
@keyframes collapse {
  from {
    max-height: 100px;
  }
  to {
    max-height: 0px;
  }
}
